@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,900&display=swap');

.btn{
  margin-left:1rem;
  padding:5px 10px;
  font-size:1.5rem;
  border:1px solid #fff;
  background: none;
  color:#fff;
  position: absolute;
  right:20%;
  bottom:0;
  animation: anim-item 0.5s ease-in-out .2s both;
  font-family: 'Montserrat';
  font-weight: 300;
  position: relative;
  top:70%;
  left: 50%;
  

  &::before{
    position: absolute;
    content:'';
    width:100%;
    height: 100%;
    border:1px solid #fff;
    left:1%;
    top:1%;
  }
 
}


@keyframes anim-item {
  0%{
    opacity:0;
    transform: translateX(20px)
  }
  100%{
    opacity: 1;
    transform: translateX(0);
  }
}

@media screen and (min-width:768px){
  .btn{
    left:60%;
  }
}

@media screen and (min-width:1024px){
  .btn{
    left:50%;
  }
}

@media screen and (max-width:767px){
  .btn{
    left:0%;
    margin:0;
    width:80%;
  }
}